@import url(https://fonts.googleapis.com/css?family=Patrick+Hand+SC);
@import url(https://fonts.googleapis.com/css?family=Patrick+Hand);
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 13px; }
  html body, html body > * {
    color: #000000;
    font-family: "Patrick Hand SC", sans-serif;
    padding: 0;
    margin: 0; }

background {
  content: " ";
  position: fixed;
  left: 0;
  right: 0;
  z-index: -999;
  display: block;
  background: url("../img/background.jpg") no-repeat fixed center;
  background-position: 50% 0%;
  background-size: cover;
  min-width: 100%;
  min-height: 100%;
  -webkit-filter: blur(7px);
  -moz-filter: blur(7px);
  -ms-filter: blur(7px);
  -o-filter: blur(7px);
  filter: blur(7px);
  -webkit-transform: scale(1.45) translateY(0px);
  -moz-transform: scale(1.45) translateY(0px);
  -ms-transform: scale(1.45) translateY(0px);
  -o-transform: scale(1.45) translateY(0px);
  transform: scale(1.45) translateY(0px); }

h1 {
  font-size: 4.5rem;
  text-align: center;
  margin: 0 0; }

header {
  display: block;
  width: 100%;
  font-size: 3.5rem;
  text-align: center; }

section.links {
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase; }
  section.links a {
    color: #2b2b2b;
    text-decoration: none;
    display: inline-block;
    padding: 0 0.5rem; }

section.board {
  max-width: 140rem;
  margin: 2em auto;
  display: flex;
  flex-flow: row wrap; }
  section.board div.column {
    flex: 1 1 20rem;
    padding: 1rem;
    text-align: center;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: flex-start;
    align-content: flex-start; }

.postit {
  position: relative;
  display: block;
  font-family: "Patrick Hand", sans-serif;
  padding: 2rem;
  margin: 1rem 0.5rem 0.5rem;
  font-size: 2.1rem;
  vertical-align: middle;
  flex: 1 1 20rem;
  max-width: 16rem;
  min-height: 11rem;
  box-shadow: 0 15px 5px 0 rgba(42, 42, 42, 0.4); }
  .postit.yellow {
    background-color: #f8ef7a;
    background: linear-gradient(135deg, #f8ef7a 0%, #f8ef7a 75%, #f7f0a3 100%); }
  .postit.blue {
    background-color: #c9d4ff;
    background: linear-gradient(135deg, #c9d4ff 0%, #c9d4ff 75%, #e0e7ff 100%); }
  .postit.pink {
    background-color: #e2acbe;
    background: linear-gradient(135deg, #e2acbe 0%, #e2acbe 75%, #ddbac5 100%); }
  .postit.avatar:after {
    content: "";
    position: absolute;
    display: block;
    right: -19px;
    bottom: -12px;
    width: 9rem;
    height: 9rem;
    z-index: 999;
    background-image: url("../img/avatar.png");
    background-size: cover;
    -webkit-filter: drop-shadow(0 5px 5px rgba(42, 42, 42, 0.4));
    -moz-filter: drop-shadow(0 5px 5px rgba(42, 42, 42, 0.4));
    -ms-filter: drop-shadow(0 5px 5px rgba(42, 42, 42, 0.4));
    -o-filter: drop-shadow(0 5px 5px rgba(42, 42, 42, 0.4));
    filter: drop-shadow(0 5px 5px rgba(42, 42, 42, 0.4)); }

